import React from 'react';
import PropTypes from 'prop-types';
import {
	Panel,
	PanelHeader,
	HeaderButton,
	platform,
	IOS,
	Div,
	View,
	Group,
	Radio,
	Form,
	Card,
	Root,
	Select,
	InfoRow,
	List,
	Cell,
	Link,
	PullToRefresh,
	Button,
	SelectMimicry,
	FormLayout,
	Input,
	Textarea,
	ScreenSpinner
} from '@vkontakte/vkui';
import persik from '../img/persik.png';
import './Persik.css';
import ReactDOM from 'react-dom'
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Done from '@vkontakte/icons/dist/24/done';


const osname = platform();

export default class Persik extends React.Component {

	constructor(props){
	super(props);

	this.state={
		title:"",
		text:"",
		mesto:"",
		setcity:"",
		street:"",
		stattime:"",
		owner_id:"",
		chatlink:"",
		type:"",
        grouplink:"",
        agreed_admin:"",
        view_owner:"",
		igo:0,
        datestop:"",
        timestop:"",
		time:"",
        types:[],
		date:"",
		bookmark:0,
		city:[],
        rr:"ав",
        token:"",
        eid:"",
		}


	}
	componentWillMount() {
		const {your_city,city,title,text,mesto,unixtime,unixtimestop,types,type,chatlink,grouplink,agreed_admin,owner_id,eid,token}=this.props;
		var date="";
		var now=new Date.UTC(Number(unixtime)*1000)
		//now.setHours(now.getHours()+2)
		var month=now.getMonth()+1;
		var day=now.getDate();
		if(day<10){day="0"+day}
		if(month<10){month="0"+month}
		var time=""
		var hour=now.getHours()
		if(hour<10){hour="0"+hour}
		var min=now.getMinutes()
		if(min<10){min="0"+min}
		time=hour+":"+min

		date=now.getFullYear().toString()+"-"+month+"-"+day

        var view_owner=1
        if(owner_id===""||owner_id==="undefined"){
            view_owner=0
        }
		this.setState({token:token,eid:eid,your_city:your_city,city:city,title:title,text:text,street:mesto.split(", ")[2],setcity:mesto.split(", ")[1],date:date,time:time,types:types,type:type,chatlink:chatlink,grouplink:grouplink,agreed_admin:agreed_admin,
        view_owner:view_owner})


         date="";
         now=new Date.UTC(Number(unixtimestop)*1000)
        //now.setHours(now.getHours()+2)
         month=now.getMonth()+1;
         day=now.getDate();
        if(day<10){day="0"+day}
        if(month<10){month="0"+month}
         time=""
         hour=now.getHours()
        if(hour<10){hour="0"+hour}
         min=now.getMinutes()
        if(min<10){min="0"+min}
        time=hour+":"+min

        date=now.getFullYear().toString()+"-"+month+"-"+day

        this.setState({datestop:date,timestop:time})


	}

	get(url){
		var xhr = new XMLHttpRequest();
		xhr.open('POST', url, false);
		xhr.send();
		return xhr.responseText;
	}
	post(url,array){
        var xhr = new XMLHttpRequest();
        xhr.open('POST', url, false);
        xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
        xhr.send(array);
        return xhr.responseText;
    }
	ret2(item,i){

		return(
			<Cell
				onClick={()=>{

					this.setState({activePanel: "home",

					})



				}}
				data-to="home"
				asideContent={this.state.your_city === item ? <Icon24Done fill="var(--accent)" /> : null}
			>
				{item}
			</Cell>

		);
	}

	Savevvf() {

        const {go,saveedit} = this.props;


        console.log("title=" + encodeURIComponent(this.state.title) + "&text=" + encodeURIComponent(this.state.text) + "&city=" + encodeURIComponent(this.state.setcity) + "&street=" + encodeURIComponent(this.state.street) +
            "&startdate=" + encodeURIComponent(this.state.date) + "&starttime=" + encodeURIComponent(this.state.time) + "&stopdate=" + encodeURIComponent(this.state.datestop) + "&stoptime" + encodeURIComponent(this.state.timestop) +
            "&type=" + encodeURIComponent(this.state.type) + "&grouplink=" + encodeURIComponent(this.state.grouplink) + "&chatlink=" + encodeURIComponent(this.state.chatlink) + "&view_owner=" + encodeURIComponent(this.state.view_owner) +
            "&agreed_admin=" + encodeURIComponent(this.state.agreed_admin) + "&id=" + this.state.eid)


        this.post("https://api.finebot.site/events/events.edit?token=" + this.state.token,
            "title=" + encodeURIComponent(this.state.title) + "&text=" + encodeURIComponent(this.state.text) + "&city=" + encodeURIComponent(this.state.setcity) + "&street=" + encodeURIComponent(this.state.street) +
            "&startdate=" + encodeURIComponent(this.state.date) + "&starttime=" + encodeURIComponent(this.state.time) + "&stopdate=" + encodeURIComponent(this.state.datestop) + "&stoptime" + encodeURIComponent(this.state.timestop) +
            "&type=" + encodeURIComponent(this.state.type) + "&grouplink=" + encodeURIComponent(this.state.grouplink) + "&chatlink=" + encodeURIComponent(this.state.chatlink) + "&view_owner=" + encodeURIComponent(this.state.view_owner) +
            "&agreed_admin=" + encodeURIComponent(this.state.agreed_admin) + "&id=" + this.state.eid)

        saveedit.call()
    }




	getCitys(item,i){
		return(
			<option value={item}>{item}</option>
		);
	}
	getSaveButton(){
	    if(this.state.title===""||this.state.text===""||this.state.setcity===""||this.state.street===""||this.state.date===""||this.state.time===""||this.state.datestop===""||this.state.timestop===""||this.state.type===""){
            return(<Button size="l" disabled stretched onClick={this.Savevvf.bind(this)}>Сохранить</Button>);
        }else{
            return(<Button size="l" stretched onClick={this.Savevvf.bind(this)}>Сохранить</Button>);
        }
    }
    setCity(v){
        var t= document.getElementById("selcity").value
        this.setState({setcity:t})
    }
    setType(v){
        var t= document.getElementById("seltype").value
        this.setState({type:t})
    }
	getTypes(item,i){
        return(
            <option value={item}>{item}</option>
        );
    }
    editTitle(e){
        const { name, value } = e.currentTarget;
	    this.setState({title:value})
    }
    editText(e){
        const { name, value } = e.currentTarget;
        this.setState({text:value})
    }
    editStreet(e){
        const { name, value } = e.currentTarget;
        this.setState({street:value})
    }
    editStartTime(e){
        const { name, value } = e.currentTarget;
        this.setState({time:value})
    }
    editStartDate(e){
        const { name, value } = e.currentTarget;
        this.setState({date:value})
    }
    editStopTime(e){
        const { name, value } = e.currentTarget;
        this.setState({timestop:value})
    }
    editStopDate(e){
        const { name, value } = e.currentTarget;
        this.setState({datestop:value})
    }
    editGroupLink(e){
        const { name, value } = e.currentTarget;
        this.setState({grouplink:value})
    }
    editChatLink(e){
        const { name, value } = e.currentTarget;
        this.setState({chatlink:value})
    }
	render() {

        var one=<Radio name="radio" onClick={()=>this.setState({agreed_admin:"0"})} value="1">Не согласовано</Radio>
        var two=<Radio name="radio" onClick={()=>this.setState({agreed_admin:"null"})} value="2">Не требуется</Radio>
        var three=<Radio name="radio" onClick={()=>this.setState({agreed_admin:"1"})} value="3">Согласовано</Radio>

        switch (this.state.agreed_admin) {
            case "1":
                three=<Radio name="radio" onClick={()=>this.setState({agreed_admin:"1"})} defaultChecked value="3">Согласовано</Radio>
                break;
            case "0":
                one=<Radio name="radio" onClick={()=>this.setState({agreed_admin:"0"})} defaultChecked value="1">Не согласовано</Radio>
                break;
            default:
                two=<Radio name="radio" onClick={()=>this.setState({agreed_admin:"null"})} defaultChecked value="2">Не требуется</Radio>
                break;
        }

        var yes=<Radio name="radio" onClick={()=>this.setState({view_owner:1})} value="1">Да</Radio>
        var no= <Radio name="radio" onClick={()=>this.setState({view_owner:0})} value="2">Нет</Radio>
        if(this.state.view_owner==1){
            yes=<Radio name="radio" onClick={()=>this.setState({view_owner:1})} defaultChecked value="1">Да</Radio>
        }else{
            no= <Radio name="radio" onClick={()=>this.setState({view_owner:0})} defaultChecked value="2">Нет</Radio>
        }

		const {go}=this.props;

		return (


			<Panel id="edit">
				<PanelHeader
					left={<HeaderButton onClick={go} data-to="full">{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}</HeaderButton>}>
					Редактирование события
				</PanelHeader>

				<Group>
					<FormLayout><Input id="title"
                                       status={this.state.title ? 'valid' : 'error'}
                                       bottom={this.state.title ? '' : 'Пожалуйста, укажите заголовок'}
                                       onChange={this.editTitle.bind(this)} defaultValue={this.state.title} top="Заголовок"></Input></FormLayout>
				</Group>
				<Group>
				<FormLayout>
					<Textarea onChange={this.editText.bind(this)}
                              status={this.state.text ? 'valid' : 'error'}
                              bottom={this.state.text ? '' : 'Пожалуйста, укажите примечание'}
                              defaultValue={this.state.text} top="Примечание" placeholder="Место, подробности" />
				</FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<Select
							top="Город"
                            onChange={()=>this.setCity()}
                            id="selcity"
							data-to="other"
                            status={this.state.setcity ? 'valid' : 'error'}
                            bottom={this.state.setcity ? '' : 'Пожалуйста, укажите город'}
							placeholder="Не выбран"
							defaultValue={this.state.your_city}
						>{this.state.your_city}
							{this.state.city.map(this.getCitys)}
						</Select>
					</FormLayout>
					<FormLayout><Input top="Улица"
                                       status={this.state.street? 'valid' : 'error'}
                                       bottom={this.state.street? '' : 'Пожалуйста, укажите улицу'}
                                       onChange={this.editStreet.bind(this)} defaultValue={this.state.street}></Input></FormLayout>
				</Group>


				<Group>
					<FormLayout>
							<Input type="date" defaultValue={this.state.date}
                                   status={this.state.date? 'valid' : 'error'}
                                   bottom={this.state.date ? '' : 'Пожалуйста, укажите дату начала'}
                                   top="Дата начала" onChange={this.editStartDate.bind(this)}/>
							<Input type="time"
                                   status={this.state.time? 'valid' : 'error'}
                                   bottom={this.state.time ? '' : 'Пожалуйста, укажите время начала'}
                                   defaultValue={this.state.time} onChange={this.editStartTime.bind(this)} top="Время начала"/>
					</FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<Input type="date" defaultValue={this.state.datestop}
                               status={this.state.datestop? 'valid' : 'error'}
                               bottom={this.state.datestop ? '' : 'Пожалуйста, укажите дату окончания'}
                               onChange={this.editStopDate.bind(this)} top="Дата конца"/>
						<Input type="time" defaultValue={this.state.timestop}
                               status={this.state.timestop? 'valid' : 'error'}
                               bottom={this.state.timestop? '' : 'Пожалуйста, укажите время окончания'}
                               onChange={this.editStopTime.bind(this)} top="Время конца"/>
					</FormLayout>
				</Group>
				<Group>
                    <FormLayout>
                        <Select
                            top="Тип"
                            id="seltype"
                            data-to="other"
                            status={this.state.type? 'valid' : 'error'}
                            bottom={this.state.type ? '' : 'Пожалуйста, укажите тип мероприятия'}
                            onChange={()=>this.setType()}
                            placeholder="Не выбран"
                            defaultValue={this.state.type}
                        >{this.state.your_city}
                            {this.state.types.map(this.getTypes)}
                        </Select>
                    </FormLayout>
				</Group>
				<Group>
					<FormLayout><Input defaultValue={this.state.grouplink} top="Ссылка на группу" onChange={this.editGroupLink.bind(this)}></Input></FormLayout>
				</Group>
				<Group>
					<FormLayout><Input defaultValue={this.state.chatlink} top="Ссылка на беседу" onChange={this.editChatLink.bind(this)}></Input></FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<div top="Показывать организатора?">
                            {yes}
                            {no}

						</div>
					</FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<div top="Согласованность с администрацией города" >
                            {one}
                            {two}
                            {three}

						</div>
					</FormLayout>
				</Group>
                <Div style={{display: 'flex'}}>
                    {this.getSaveButton()}
                </Div>
			</Panel>

		);
	}
}
const style={
	fd: {

		width: '100%',
		display: 'flex',
		height: '100%',
		justifyContent: 'bottom',


	},
	buttons:{
		height: '100%',
		padding: '3px',
		width: '100%',


	},
};
Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};


