	load(){
		if(this.state.events.length==0){

			var exp=""
			var re=this.get("https://api.finebot.site/events/events.get?token="+this.state.token)
			re=JSON.parse(re)
			var i =0
			for(i;i<re.response.length;i++){

				var mesto=re.response[i].country+", "+re.response[i].city+", "+re.response[i].street
				this.state.events.push(encodeURIComponent(re.response[i].title)+"&&&"+encodeURIComponent(mesto)+"&&&"+encodeURIComponent(re.response[i].starttime)+"&&&"+encodeURIComponent(re.response[i].chatlink)+"&&&"+encodeURIComponent(re.response[i].owner_id)+"&&&"+encodeURIComponent(re.response[i].text)+"&&&"+encodeURIComponent(re.response[i].type)+"&&&"+encodeURIComponent(re.response[i].grouplink)+"&&&"+encodeURIComponent(re.response[i].agreed_admin)+"&&&"+(re.response[i].can_edit)+"&&&"+encodeURIComponent(re.response[i].unixtime)+"&&&"+encodeURIComponent(re.response[i].unixtimestop)+"&&&"+encodeURIComponent(re.response[i].id)
					+"&&&"+encodeURIComponent(re.response[i].igo))
			}
			for(i=0;i<re.igo.length;i++){

				var mesto=re.igo[i].country+", "+re.igo[i].city+", "+re.igo[i].street
				this.state.igos.push(encodeURIComponent(re.igo[i].title)+"&&&"+encodeURIComponent(mesto)+"&&&"+encodeURIComponent(re.igo[i].starttime)+"&&&"+encodeURIComponent(re.igo[i].chatlink)+"&&&"+encodeURIComponent(re.igo[i].owner_id)+"&&&"+encodeURIComponent(re.igo[i].text)+"&&&"+encodeURIComponent(re.igo[i].type)+"&&&"+encodeURIComponent(re.igo[i].grouplink)+"&&&"+encodeURIComponent(re.igo[i].agreed_admin)+"&&&"+(re.igo[i].can_edit)+"&&&"+encodeURIComponent(re.igo[i].unixtime)+"&&&"+encodeURIComponent(re.igo[i].unixtimestop)+"&&&"+encodeURIComponent(re.igo[i].id)
					+"&&&"+encodeURIComponent(re.igo[i].igo))
			}
			this.setState({your_city:re.your_city,city:re.city,types:re.types})

		}
		this.setState({fetching:false})
		this.setState({popout:null})



		if(this.state.igos.length==0){
			this.setState({pustigos:<Div style={{ paddingTop: 60, paddingBottom: 60, color: 'gray', width:'100%',textAlign:'center',fontSize:24}}>Вы еще никуда не собирались пойти</Div>})
		}else{
			this.setState({pustigos:""})
		}
		if(this.state.events.length==0){
			this.setState({test:<Div style={{ paddingTop: 60, paddingBottom: 60, color: 'gray', width:'100%',textAlign:'center',fontSize:24}}>Тут пусто :(</Div>})
			this.openDefault("Ошибка","Мы ничего не нашли в Вашем городе :(")
		}else{
			this.setState({test:null})

		}
		//document.getElementById("test").

	}
